<template>
	<view class="detail">
		<view class="container">
			<view v-if="loadState">
				<u-skeleton rows="5" title loading avatar></u-skeleton>
			</view>
			<view v-else>
				<view class="title">{{dataObj.title}}</view>
				<view class="userinfo">
					<view class="avatar">
						<image :src="dataObj.user_id[0].avatar_file?dataObj.user_id[0].avatar_file.url :'../../static/images/user-default.jpg'"
							mode="aspectFill">
						</image>
					</view>
					<view class="text">
						<view class="name">
							{{dataObj.user_id[0].nickname ||  dataObj.user_id[0].username || dataObj.user_id[0].mobile||'请设置昵称'}}
						</view>
						<view class="small">
							<uni-dateformat :date="dataObj.publish_date" format="yyyy年MM月dd hh:mm:ss">
							</uni-dateformat> · 发布于{{dataObj.province}}
						</view>
					</view>
				</view>
				<view class="content">
					<u-parse :content="dataObj.content" :tagStyle="tagStyle"></u-parse>
				</view>
				<view class="like">
					<view class="btn" @click="clickLike" :class="dataObj.isLike? 'active':''">
						<text class="iconfont icon-good-fill"></text>
						<text v-if="dataObj.like_count>0">{{dataObj.like_count}}</text>
					</view>
					<view class="users">
						<template v-for="item in likeUserArr">
							<image :src="giveAvatar(item)" mode="aspectFill"></image>
						</template>
					</view>
					<view class="text"><text class="num">{{dataObj.view_count}}</text>人看过</view>
				</view>
			</view>
		</view>
		<!-- 评论模块 -->
		<view class="comment">
			<view v-if="!commentList.length && noComment" style="padding: 50rpx;">
				<u-empty text="暂无评论" icon="http://cdn.uviewui.com/uview/empty/comment.png"></u-empty>
			</view>
			<view class="content" v-if="commentList.length">
				<view class="item" v-for="(item,index) in commentList" :key="item._id">
					<comment-item :item="item" @removeEnv="PremoveEnv"></comment-item>
				</view>
			</view>
		</view>
		<comment-frame :commentObj='commentObj' @commentEnv='PcommentEnv'></comment-frame>
	</view>
</template>

<script>
	import pagesJson from '@/pages.json'
	import { giveName, likeFun, giveAvatar } from '../../utils/tools.js'
	import { store } from '../../uni_modules/uni-id-pages/common/store.js'
	const db = uniCloud.database()
	const dbCmd = db.command
	const utilsObj = uniCloud.importObject('utilsObj', { customUI: true })
	export default {
		data() {
			return {
				artid: '',
				tagStyle: {
					p: 'color: black;font-size:32rpx;line-height:1.7em;padding-bottom:10rpx;',
					img: 'margin:10rpx 0;'
				},
				dataObj: null,
				loadState: true,
				likeTime: Date.now(),
				likeUserArr: [],
				commentObj: {
					article_id: '',
					comment_type: 0,
				},
				commentList: [],
				noComment: false
			}
		},
		onLoad(e) {
			if (!e.id) {
				this.errFun()
			}
			this.artid = e.id
			this.getData()
			this.commentObj.article_id = e.id
			this.readUpdate()
			this.getUserLike()
			this.getComment()
		},
		methods: {
			// 删除评论的回调
			PremoveEnv(e) {
				console.log(e);
				let index = this.commentList.findIndex((item) => {
					return item._id == e.id
				})
				this.commentList.splice(index, 1)
			},
			// 评论成功后的回调
			PcommentEnv(e) {
				console.log(e);
				this.commentList.unshift({
					...e,
					...this.commentObj,
					user_id: [{ ...store.userInfo }]
				})
				console.log(this.commentList);
			},
			// 获取评论列表
			async getComment() {
				let commentTemp = db.collection('quanzi_comment')
					.where(`article_id=='${this.artid}' && comment_type==0`)
					.orderBy('comment_date desc')
					.limit(5).getTemp()
				let userTemp = db.collection('uni-id-users').field('avatar_file,_id,nickname,username').getTemp()
				let res = await db.collection(commentTemp, userTemp).get()
				let idArr = res.result.data.map((item) => { return item._id })
				let replyArr = await db.collection('quanzi_comment')
					.where({ reply_comment_id: dbCmd.in(idArr) })
					.groupBy('reply_comment_id')
					.groupField('count(*) as totalReply')
					.get()
				res.result.data.forEach(
					(item) => {
						let index = replyArr.result.data.findIndex(find => {
							return find.reply_comment_id == item._id
						})
						if (index > -1) item.totalReply = replyArr.result.data[index].totalReply
					}
				)
				if (res.result.data.length == 0) this.noComment = true
				this.commentList = res.result.data
				console.log(this.commentList, 'commentList');
			},
			// 获取点赞用户
			async getUserLike() {
				let likeTemp = await db.collection('quanzi_like').where(`article_id=='${this.artid}'`).getTemp()
				let userTemp = db.collection('uni-id-users').field('_id,avatar_file').getTemp()
				let res = await db.collection(likeTemp, userTemp).orderBy('publish_date desc').limit(5).get()
				res.result.data = res.result.data.reverse()
				this.likeUserArr = res.result.data
				console.log(this.likeUserArr)
			},
			// 点赞操作
			async clickLike() {
				if (!store.hasLogin) {
					return uni.showModal({
						title: '登陆之后才能点赞',
						content: '确定登陆吗？',
						success: (res) => {
							if (res.confirm) {
								uni.navigateTo({
									url: "/" + pagesJson.uniIdRouter.loginPage
								})
							}
						}
					})
				}
				let time = Date.now()
				if (time - this.likeTime < 1000) {
					uni.showToast({
						title: '请不要频繁点击',
						icon: 'none'
					})
					return
				}
				this.likeTime = time
				this.dataObj.isLike = !this.dataObj.isLike
				this.dataObj.isLike ? this.dataObj.like_count++ : this.dataObj.like_count--
				// 底部点赞头像无感更新
				let likeUserArrTemp = this.likeUserArr[this.likeUserArr.length - 1]
				if (!this.dataObj.isLike) {
					this.likeUserArr.splice(this.likeUserArr.length - 1)
				} else {
					let uniid = uni.getStorageSync('uni-id-pages-userInfo')
					let temp = {
						user_id: [{ avatar_file: { url: uniid?.avatar_file?.url || '../../static/images/user-default.jpg' } }]
					}
					console.log(this.likeUserArr)
					this.likeUserArr.push(temp)
				}
				// 调用点赞方法
				likeFun(this.artid)
			},
			// 修改阅读量
			readUpdate() {
				utilsObj.operation('quanzi_article', 'view_count', this.artid, 1).then(res => {
					console.log(res);
				})
			},
			errFun() {
				uni.showToast({
					title: '参数有误',
					icon: 'none'
				})
				setTimeout(() => {
					uni.reLaunch({
						url: '/pages/index/index'
					})
				}, 800)
			},
			getData() {
				let artTemp = db.collection('quanzi_article').where(`_id=='${this.artid}'`).getTemp()
				let userTemp = db.collection('uni-id-users').field('_id,username,nickname,avatar_file').getTemp()
				let likeTemp = db.collection('quanzi_like').where(`article_id=='${this.artid}' && user_id==$cloudEnv_uid`).getTemp()
				let tempArr = [artTemp, userTemp]
				if (store.hasLogin) tempArr.push(likeTemp)
				db.collection(...tempArr).get({ getOne: true }).then(res => {
					if (res.result.data) {
						this.dataObj = res.result.data
						console.log(this.dataObj, 'dataObj');
						this.loadState = false
						let isLike = false
						if (store.hasLogin) isLike = res.result.data._id.quanzi_like.length ? true : false
						this.dataObj.isLike = isLike
						uni.setNavigationBarTitle({
							title: this.dataObj.title
						})
					} else {
						this.errFun()
						return
					}
				})
			},
			giveName,
			giveAvatar,

		}
	}
</script>

<style lang="scss">
	.detail {
		background: #f8f8f8;
		min-height: calc(100vh - var(--window-top));

		.container {
			padding: 30rpx;
			background: #fff;

			.title {
				font-size: 46rpx;
				color: #333;
				line-height: 1.4em;
				font-weight: 600;
			}

			.userinfo {
				padding: 20rpx 0 50rpx;
				display: flex;
				align-items: center;

				.avatar {
					width: 60rpx;
					height: 60rpx;
					padding-right: 15rpx;

					image {
						width: 100%;
						height: 100%;
						border-radius: 50%;
					}
				}

				.text {
					font-size: 28rpx;
					color: #555;

					.small {
						font-size: 20rpx;
						color: #999;
					}
				}
			}

			.content {}

			.like {
				display: flex;
				flex-direction: column;
				align-items: center;
				padding: 80rpx 50rpx 50rpx;

				.btn {
					width: 260rpx;
					height: 120rpx;
					background: #e4e4e4;
					border-radius: 100rpx;
					color: #fff;
					display: flex;
					justify-content: center;
					align-items: center;
					flex-direction: column;
					font-size: 28rpx;

					.iconfont {
						font-size: 50rpx;
					}

					&.active {
						background: #0199FE;
					}
				}

				.text {
					font-size: 26rpx;
					color: #666;

					.num {
						color: #0199FE
					}

					.spot {
						padding: 0 10rpx;
					}
				}

				.users {
					display: flex;
					justify-content: center;
					padding: 30rpx 0;

					image {
						width: 50rpx;
						height: 50rpx;
						border-radius: 50%;
						border: 3px solid #fff;
						margin-left: -20rpx;
					}
				}
			}
		}

		.comment {
			padding: 30rpx;
			padding-bottom: 120rpx;

			.item {
				padding: 10rpx 0;
			}
		}
	}
</style>
